a {
  cursor: pointer;
  transition: color 0.3s;
}
a:hover {
  text-decoration: none;
}
a:visited {
  color: #337ab7;
}
.panel-heading .title {
  font-size: 1.4em;
}
.btn.btn-in-table {
  padding: 3px 8px;
  margin: 0;
  line-height: 1;
}
.btn.btn-no-padding {
  padding: 0;
}
.list-group {
  margin: 20px 0;
}
.list-group.with-border .list-group-item {
  border: 1px solid #ddd;
}
.list-group .list-group-item {
  text-shadow: 0 0 3px rgba(255, 255, 255, 0.9);
  padding: 5px 15px;
}
.list-group .list-group-item.upload-ready {
  background-color: #fcf8e3;
}
.list-group .list-group-item.upload-success {
  background-color: #dff0d8;
}
.list-group .list-group-item.upload-fail {
  background-color: #f2dede;
}
.list-group .list-group-item.with-progress .progress {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  margin: 0;
  padding: 0;
  height: 100%;
  border-radius: 0;
  border: 0;
  background-color: transparent;
  box-shadow: none;
  opacity: 0.5;
}
.list-group .list-group-item .btn {
  padding: 0 3px;
  margin: 0;
}
form {
  position: relative;
}
@-webkit-keyframes blink {
  0% {
    background: rgba(0, 0, 0, 0.5);
  }
  50% {
    background: rgba(0, 0, 0, 0.3);
  }
  100% {
    background: rgba(0, 0, 0, 0.5);
  }
}
@keyframes blink {
  0% {
    background: rgba(0, 0, 0, 0.5);
  }
  50% {
    background: rgba(0, 0, 0, 0.3);
  }
  100% {
    background: rgba(0, 0, 0, 0.5);
  }
}
form .ajaxing-mask {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: rgba(0, 0, 0, 0.3);
  z-index: 99;
  -webkit-animation-direction: normal;
  animation-direction: normal;
  -webkit-animation-duration: 2s;
  animation-duration: 2s;
  -webkit-animation-iteration-count: infinite;
  animation-iteration-count: infinite;
  -webkit-animation-name: blink;
  animation-name: blink;
  -webkit-animation-timing-function: ease;
  animation-timing-function: ease;
}
form .ajaxing-mask .progress {
  margin: 0;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  height: 10px;
}

